import React, { useState, useEffect } from 'react';
import { Card, Table } from 'antd';
import { columns } from './columns';
import { getUserList } from './fetch';

export default () => {
    const [userList, setUserList] = useState<UserList>([]);
    const fetchData = () => {
        getUserList().then(data => {
            setUserList(data);
        });
    };
    useEffect(() => {
        fetchData();
    }, []);
    return (
        <div>
            <Card
                title="用户列表"
            >
                <Table
                    bordered
                    rowKey="user_id"
                    columns={columns}
                    dataSource={userList}
                >
                </Table>
            </Card>
        </div>
    );
};
