import React, { useState, useEffect } from 'react';
import { Card, Table } from 'antd';
import { columns } from './columns';
import { fetchDeliverList } from './fetch';


export default () => {
    const [userList, setUserList] = useState<UserList>([]);
    const fetchData = () => {
        fetchDeliverList().then(data => {
            setUserList(data);
        });
    };
    useEffect(() => {
        fetchData();
    }, []);
    return (
        <div>
            <Card
                title="配送员列表"
            >
                <Table
                    rowKey="man_id"
                    bordered
                    columns={columns}
                    dataSource={userList}
                >

                </Table>
            </Card>
        </div>
    )
}
