import React, { useState, useEffect } from "react";
import {
    Table,
    Button,
    Modal,
    Form,
    Upload,
    Card,
    Input,
    Select,
    Tag,
    InputNumber,
    message,
} from "antd";
import { LoadingOutlined, PlusOutlined } from "@ant-design/icons";
import { getDrinkList, getMaterialList, actionDrink } from "./fetch";
import { getColumns } from "./column";
import { upload } from "../../utils";
import "./index.css";

const { Item: FormItem } = Form;
const { Option } = Select;
const layout = {
    wrapperCol: { span: 24 },
};
const defaultDrink = {
    drink_avatartUrl: "",
    drink_form: [],
    drink_name: "",
    drink_price: 0,
};

export default () => {
    const [visible, setVisible] = useState(false);
    const [loading, setLoading] = useState(false);
    const [tagVisible, setTagVisible] = useState(false);
    const [status, setStatus] = useState(1);
    const [materials, setMaterials] = useState<MaterialList>([]);
    const [drinkList, setDrinkList] = useState<DrinkList>([]);
    const [drink, setDrink] = useState<Drink>(defaultDrink);
    const [form] = Form.useForm();
    const newMaterial = { name: "", value: 0 };
    const onUpload = async (options: any) => {
        const { file } = options;
        setLoading(true);
        const timestamp = Date.now().toString();
        await upload(file, timestamp);
        setLoading(false);
        setDrink({
            ...drink,
            drink_avatartUrl: `https://img.qinyongli.cn/${timestamp}`,
        });
    };
    const onSubmit = (values: any) => {
        actionDrink({
            ...drink,
            drink_name: values.drink_name,
            drink_price: values.drink_price,
        }, status).then(data => {
            message.success('提交成功');
            fetchData();
            setVisible(false);
        });
    };
    const onMaterialConfirm = () => {
        setDrink({
            ...drink,
            drink_form: [...drink.drink_form, newMaterial],
        });
        setTagVisible(false);
    };
    const onMaterialSelect = (value: string) => {
        newMaterial.name = value;
    };
    const onMaterialChange = (value: undefined | number) => {
        if (value) {
            newMaterial.value = value;
        }
    };
    const uploadButton = (
        <div>
            {loading ? <LoadingOutlined /> : <PlusOutlined />}
            <div>上传</div>
        </div>
    );
    const onAdd = () => {
        setStatus(1);
        setVisible(true);
        setDrink(defaultDrink);
        form.setFieldsValue({
            drink_name: defaultDrink.drink_name,
            drink_price: defaultDrink.drink_price,
            drink_avatartUrl: defaultDrink.drink_avatartUrl,
        });
    };
    const onDel = (drink: Drink) => {
        actionDrink(drink, 2).then(data => {
            fetchData();
            message.success('删除成功');
        })
    };
    const onMaterialCancel = () => {
        setTagVisible(false);
    };
    const onEdit = (drink: Drink) => {
        setStatus(3);
        setVisible(true);
        setDrink(drink);
        form.setFieldsValue({
            drink_name: drink.drink_name,
            drink_price: drink.drink_price,
            drink_avatartUrl: drink.drink_avatartUrl,
        });
        
    };
    const onClose = (material: NameValue) => {
        drink.drink_form = drink.drink_form.filter((item: NameValue) => {
            return material !== item;
        });
    };  
    const columns = getColumns({ onDel, onEdit });
    const fetchData = () => {
        getDrinkList().then((data) => {
            setDrinkList(data);
        });
        getMaterialList().then((data) => {
            setMaterials(data);
        });
    }
    useEffect(() => {
        fetchData();
    }, []);
    return (
        <>
            <Card
                title="饮料配置"
                extra={
                    <Button type="primary" onClick={onAdd}>
                        添加
                    </Button>
                }
            >
                <Table
                    rowKey="drink_name"
                    bordered
                    dataSource={drinkList}
                    columns={columns}
                />
            </Card>
            <Modal
                title={status === 1 ? "添加饮料" : "编辑饮料"}
                visible={visible}
                footer={null}
                onCancel={() => setVisible(false)}
            >
                <Form
                    layout="vertical"
                    form={form}
                    onFinish={onSubmit}
                    {...layout}
                >
                    <FormItem name="drink_name" label="名称" required>
                        <Input
                            disabled={status === 3}
                            width="100%"
                            placeholder="饮料名称"
                        />
                    </FormItem>
                    <FormItem name="drink_form" label="原料" required>
                        <div
                            style={{
                                display: "flex",
                                alignItems: "flex-end",
                                flexWrap: "wrap",
                            }}
                        >
                            {drink.drink_form.map((item: any, index) => {
                                return (
                                    <Tag closable onClose={() => onClose(item)} key={index}>
                                        {`${item.name} ${item.value}g`}
                                    </Tag>
                                );
                            })}
                            {tagVisible ? (
                                null
                            ) : (
                                <Tag
                                    className="tag-plus"
                                    onClick={() => setTagVisible(true)}
                                >
                                    <PlusOutlined />
                                </Tag>
                            )}
                        </div>
                        {
                            tagVisible ? (
                                <div style={{
                                    marginTop: 5
                                }}>
                                    <Select
                                        style={{ width: 78 }}
                                        size="small"
                                        onChange={onMaterialSelect}
                                    >
                                        {materials.map((item) => {
                                            return (
                                                <Option
                                                    disabled={drink.drink_form.some((form: NameValue) => {
                                                        return form.name === item.choice_name;
                                                    })}
                                                    value={item.choice_name}
                                                >
                                                    {item.choice_name}
                                                </Option>
                                            );
                                        })}
                                    </Select>
                                    <InputNumber
                                        style={{ width: 50 }}
                                        size="small"
                                        min={1}
                                        defaultValue={1}
                                        onChange={onMaterialChange}
                                        onPressEnter={onMaterialConfirm}
                                    />
                                    <Button
                                        onClick={onMaterialCancel}
                                        size="small"
                                    >
                                        取消
                                    </Button>
                                    <Button
                                        onClick={onMaterialConfirm}
                                        type="primary"
                                        size="small"
                                    >
                                        确认
                                    </Button>
                                </div>
                            ) : null
                        }
                    </FormItem>
                    <FormItem name="drink_price" label="价格" required>
                        <InputNumber width="100%" placeholder="饮料价格" />
                    </FormItem>
                    <FormItem name="drink_avatartUrl" label="缩略图" required>
                        <Upload
                            listType="picture-card"
                            showUploadList={false}
                            customRequest={onUpload}
                        >
                            {drink.drink_avatartUrl ? (
                                <img
                                    src={drink.drink_avatartUrl}
                                    alt="thumbnailUrl"
                                    style={{ width: "100%" }}
                                />
                            ) : (
                                uploadButton
                            )}
                        </Upload>
                    </FormItem>
                    <FormItem style={{ textAlign: "center" }}>
                        <Button type="primary" htmlType="submit">
                            提交
                        </Button>
                    </FormItem>
                </Form>
            </Modal>
        </>
    );
};
