import React, { useState, useEffect } from 'react';
import { Card, Button, Table, Modal, Form, Input, message } from 'antd';
import { getColumns } from './columns';
import { getMaterialList, actionMaterial } from './fetch';

const { Item: FormItem } = Form;
const layout = {
    wrapperCol: { span: 24 }
};

export default () => {
    const [visible, setVisible] = useState(false);
    const [status, setStatus] = useState(0);
    const [materialList, setMaterialList] = useState<MaterialList>([]);
    const [form] = Form.useForm();
    const fetchData = () => {
        getMaterialList().then((data: MaterialList) => {
            setMaterialList(data);
        });
    };
    const onSubmit = (values: Obj) => {
        actionMaterial({
            name: values.choice_name,
            number: values.choice_num,
            status,
        }).then(data => {
            setVisible(false);
            message.success('提交成功');
            fetchData();
        });
    };
    const onAdd = () => {
        setStatus(1);
        setVisible(true);
        form.setFieldsValue({
            choice_name: '',
            choice_num: ''
        });
    };
    const onEdit = (name: string, record: Obj) => {
        setStatus(3);
        setVisible(true);
        form.setFieldsValue(record);
    };
    const onDel = (name: string, record: Obj) => {
        actionMaterial({
            name,
            number: record.choice_num,
            status: 2,
        }).then(data => {
            message.success('删除成功');
            fetchData();
        });
    };
    useEffect(() => {
        fetchData();
    }, []);
    const columns = getColumns({ onDel, onEdit });
    return (
        <div>
            <Card
                title="原料配置"
                extra={
                    <Button onClick={onAdd} type="primary">添加</Button>
                }
            >
                <Table
                    rowKey="choice_name"
                    dataSource={materialList}
                    columns={columns}
                    bordered
                >
                </Table>
            </Card>
            <Modal
                title={status === 1 ? '添加原料' : '编辑原料'}
                visible={visible}
                footer={null}
                onCancel={() => setVisible(false)}
            >
                <Form
                    {...layout}
                    form={form}
                    onFinish={onSubmit}
                    layout="vertical"
                >
                    <FormItem name="choice_name" label="名称" required>
                        <Input disabled={status === 3} width="100%" placeholder="原料名称" />
                    </FormItem>
                    <FormItem name="choice_num" label="余量" required>
                        <Input width="100%" placeholder="原料余量" />
                    </FormItem>
                    <FormItem style={{ textAlign: 'center' }}>
                        <Button type="primary" htmlType="submit">提交</Button>
                    </FormItem>
                </Form>
            </Modal>
        </div>
    )
}
