import React, { useState, useEffect } from 'react';
import { Card, Table, Radio } from 'antd';
import { RadioChangeEvent } from 'antd/lib/radio'
import { getColumns } from './columns';
import { getOrderList } from './fetch';
import './index.css';


export default () => {
    let orders: Array<any> = [];
    const [status, setStatus] = useState(-1);
    const [allOrders, setAllOrders] = useState([]);
    const handleChange = (e: RadioChangeEvent) => {
        setStatus(e.target.value);
    };
    useEffect(() => {
        getOrderList().then(data => {
            setAllOrders(data);
        });
    }, []);
    if (status === -1) {
        orders = allOrders;
    } else {
        orders = allOrders.filter((order: any) => {
            return order.status === status;
        });
    }
    const columns = [...getColumns({})];
    return (
        <div>
            <Card
                title="订单列表"
                extra={
                    <Radio.Group
                        value={status}
                        onChange={handleChange}
                    >
                        <Radio.Button value={-1}>全部</Radio.Button>
                        <Radio.Button value={1}>制作中</Radio.Button>
                        <Radio.Button value={2}>配送中</Radio.Button>
                        <Radio.Button value={3}>完成订单</Radio.Button>
                    </Radio.Group>
                }
            >
                <Table
                    rowKey="order_id"
                    bordered
                    columns={columns}
                    dataSource={orders}
                />
            </Card>
        </div>
    )
}
