import * as qiniu from 'qiniu-js';
import { uploadToken as token } from './constant';

export const upload = (file: Blob, key: string) => {
    const observable = qiniu.upload(file, key, token, {}, {});
    return new Promise((resolve, reject) => {
        observable.subscribe({
            next(res){
            },
            error(err){
                reject(err)
            }, 
            complete(res){
                resolve(res)
            },
        });
    });
};