import React from 'react';
import { Button } from 'antd';

export const getColumns = (action: Obj) => {
    const columns = [
        {
            title: "序号",
            dataIndex: "id",
            render(value: string, record: any, index: number) {
                return index + 1;
            }
        },
        {
            title: "名称",
            dataIndex: "drink_name",
        },
        {
            title: "原料",
            dataIndex: "drink_form",
            render(values: any[]) {
                const list = values.map((item, index) => (
                    <div key={index}>
                        { `${item.name}: ${item.value}g` }
                    </div>
                ));
                return (
                    <>
                        { list }
                    </>
                )
            }
        },
        {
            title: "价格",
            dataIndex: "drink_price",
        },
        {
            title: "缩略图",
            width: 150,
            dataIndex: "drink_avatartUrl",
            render(value: string) {
                return (
                    <img width="100%" src={value} alt=""/>
                )
            }
        },
        {
            title: "操作",
            dataIndex: "id",
            render(id: number, record: Drink) {
                return (
                    <>
                        <Button onClick={() => action.onDel(record)} type="primary" size="small">删除</Button>
                        <Button onClick={() => action.onEdit(record)} style={{ marginLeft: 10 }} type="primary" size="small">编辑</Button>
                    </>
                )
            }
        }
    ];
    return columns;
}

export const getCustomColumns = (action: Obj) => {
    const columns = [
        {
            title: '名称',
            dataIndex: 'drink_name'
        },
        {
            title: '可选原料',
            dataIndex: 'material_options',
            render(value: string[]) {
                return (
                    value.join('、')
                );
            }
        },
        {
            title: '价格',
            dataIndex: 'drink_price'
        },
        {
            title: '容量',
            dataIndex: 'drink_capacity'
        },
        {
            title: '缩略图',
            dataIndex: "drink_avatartUrl",
            width: 150,
            render(value: string) {
                return (
                    <img width="100%" src={value} alt=""/>
                )
            }
        },
        {
            title: '操作',
            render(id: number, record: DrinkCustom) {
                return (
                    <>
                        <Button onClick={() => action.onEdit(record)} style={{ marginLeft: 10 }} type="primary" size="small">编辑</Button>
                    </>
                )
            }
        },
    ];
    return columns;
};