import axios from 'axios';

export const getMaterialList = () => {
    return axios.get('/manage/choice') as Promise<MaterialList>;
};

export const getDrinkList = () => {
    return axios.get('/manage/drink') as Promise<DrinkList>;
};

export const actionDrink = (drink: Drink, status: number) => {
    return axios.post('/manage/drink', {
        ...drink,
        status,
    });
};

export const getDrinkSelf = () => {
    return axios.get('/manage/drinkself') as Promise<DrinkCustom[]>;
};

export const saveDrinkSelf = (drink: DrinkCustom) => {
    return axios.post('/manage/drinkself', {
        ...drink,
        status: 3,
    });
};
