import React, { useState, useEffect } from "react";
import {
    Card,
    Table,
    Modal,
    Form,
    Input,
    Select,
    Tag,
    InputNumber,
    Upload,
    Button, 
    message,
} from "antd";
import { LoadingOutlined, PlusOutlined } from "@ant-design/icons";
import { getCustomColumns } from "./column";
import { upload } from "../../utils";
import { getMaterialList, getDrinkSelf, saveDrinkSelf } from "./fetch";
const { Option } = Select;
const { Item: FormItem } = Form;
const layout = {
    wrapperCol: { span: 24 },
};
const defaultDrink = {
    drink_name: "",
    drink_avatartUrl: "",
    material_options: [],
    drink_price: 0,
    drink_capacity: 0,
};

export default () => {
    const [visible, setVisible] = useState(false);
    const [loading, setLoading] = useState(false);
    const [tagVisible, setTagVisible] = useState(false);
    const [current, setCurrent] = useState('');
    const [drink, setDrink] = useState<DrinkCustom>(defaultDrink);
    const [drinkList, setDrinkList] = useState<DrinkCustom[]>([])
    const [materials, setMaterials] = useState<MaterialList>([]);
    const onUpload = async (options: any) => {
        const { file } = options;
        setLoading(true);
        const timestamp = Date.now().toString();
        await upload(file, timestamp);
        setLoading(false);
        setDrink({
            ...drink,
            drink_avatartUrl: `https://img.qinyongli.cn/${timestamp}`,
        });
    };
    const onSubmit = (values: any) => {
        saveDrinkSelf({
            ...drink,
            drink_price: values.drink_price,
            drink_capacity: values.drink_capacity,
        }).then((res) => {
            message.success('编辑成功');
            fetchData();
            setVisible(false);
        });
    };
    const onEdit = (drinkCustom: DrinkCustom) => {
        setDrink(drinkCustom);
        setVisible(true);
        form.setFieldsValue({
            drink_name: drinkCustom.drink_name,
            drink_price: drinkCustom.drink_price,
            drink_avatartUrl: drinkCustom.drink_avatartUrl,
            drink_capacity: drinkCustom.drink_capacity,
        });
    };
    const onMaterialSelect = (value: string) => {
        setCurrent(value);
    };
    const onMaterialConfirm = () => {
        if (current) {
            drink.material_options.push(current);
            setDrink({
                ...drink
            });
            setCurrent('');
        }
        setTagVisible(false);
    };
    const onClose = (material: string) => {
        let material_options = drink.material_options.filter((item: string) => {
            return material !== item;
        });
        setDrink({
            ...drink,
            material_options: [...material_options],
        });
    };  
    const uploadButton = (
        <div>
            {loading ? <LoadingOutlined /> : <PlusOutlined />}
            <div>上传</div>
        </div>
    );
    const fetchData = () => {
        getMaterialList().then((data) => {
            setMaterials(data);
        });
        getDrinkSelf().then(data => {
            setDrinkList(data);
        });
    };
    useEffect(() => {
        fetchData();
    }, []);
    const columns = getCustomColumns({
        onEdit,
    });
    const [form] = Form.useForm();
    return (
        <>
            <Card title="自配饮料配置">
                <Table bordered rowKey="drink_name" columns={columns} dataSource={drinkList}></Table>
            </Card>
            <Modal
                title="编辑饮料"
                visible={visible}
                footer={null}
                onCancel={() => setVisible(false)}
            >
                <Form
                    layout="vertical"
                    form={form}
                    onFinish={onSubmit}
                    {...layout}
                >
                    <FormItem name="drink_name" label="名称" required>
                        <Input
                            disabled
                            width="100%"
                            placeholder="饮料名称"
                        />
                    </FormItem>
                    <FormItem name="material_options" label="原料(最多四种)" required>
                        <div
                            style={{
                                display: "flex",
                                alignItems: "flex-end",
                                flexWrap: "wrap",
                            }}
                        >
                            {drink.material_options.map((item: any, index) => {
                                return (
                                    <Tag
                                        closable
                                        onClose={() => onClose(item)}
                                        key={index}
                                    >
                                        {item}
                                    </Tag>
                                );
                            })}
                            {tagVisible ?  (
                                <Select
                                    style={{ width: 78 }}
                                    size="small"
                                    onChange={onMaterialSelect}
                                    onBlur={onMaterialConfirm}
                                    value={current}
                                >
                                    {materials.map((item) => {
                                        return (
                                            <Option
                                                disabled={drink.material_options.some(
                                                    (formName: string) => {
                                                        return (
                                                            formName ===
                                                            item.choice_name
                                                        );
                                                    }
                                                )}
                                                value={item.choice_name}
                                            >
                                                {item.choice_name}
                                            </Option>
                                        );
                                    })}
                                </Select>
                            ) : (
                                drink.material_options.length >= 4 ?
                                null : (
                                    <Tag
                                        className="tag-plus"
                                        onClick={() => setTagVisible(true)}
                                    >
                                        <PlusOutlined />
                                    </Tag>
                                )
                            )}
                        </div>
                    </FormItem>
                    <FormItem name="drink_capacity" label="容量" required>
                        <InputNumber width="100%" placeholder="饮料容量" />
                    </FormItem>
                    <FormItem name="drink_price" label="价格" required>
                        <InputNumber width="100%" placeholder="饮料价格" />
                    </FormItem>
                    <FormItem name="drink_avatartUrl" label="缩略图" required>
                        <Upload
                            listType="picture-card"
                            showUploadList={false}
                            customRequest={onUpload}
                        >
                            {drink.drink_avatartUrl ? (
                                <img
                                    src={drink.drink_avatartUrl}
                                    alt="thumbnailUrl"
                                    style={{ width: "100%" }}
                                />
                            ) : (
                                uploadButton
                            )}
                        </Upload>
                    </FormItem>
                    <FormItem style={{ textAlign: "center" }}>
                        <Button type="primary" htmlType="submit">
                            提交
                        </Button>
                    </FormItem>
                </Form>
            </Modal>
        </>
    );
};
