import React from 'react';
import { Button } from 'antd';

export const getColumns = (action: Obj) => {
    const columns = [
        {
            title: "序号",
            dataIndex: "id",
            render(id: string, record: any, index: number) {
                return index + 1;
            }
        },
        {
            title: "名称",
            dataIndex: "choice_name",
        },
        {
            title: "余量",
            dataIndex: "choice_num",
        },
        {
            title: "操作",
            dataIndex: "choice_name",
            render(id: string, record: Obj) {
                return (
                    <div>
                        <Button onClick={() => action.onDel(id, record)} size="small" type="primary">删除</Button>
                        <Button onClick={() => action.onEdit(id, record)} size="small" style={{ marginLeft: 10 }} type="primary">编辑</Button>
                    </div>
                )
            }
        }
    ];
    return columns;
}
