import React, { useState, useRef, useEffect } from 'react';
import { Layout, Menu } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import { HashRouter as Router, Route, Switch, Link } from 'react-router-dom';
import Drink from './pages/drink';
import User from './pages/user';
import Deliver from './pages/deliver';
import Orders from './pages/orders';
import Material from './pages/material';
import './App.css';
import { SelectParam } from 'antd/lib/menu';
const { Header, Content, Footer, Sider } = Layout

function App(): JSX.Element {
    const ref = useRef<Router>(null);
    const [current, setCurrent] = useState('');
    const handleLogoClick = () => {
        setCurrent('material');
    };
    const handleSelect = (param: SelectParam) => {
        setCurrent(param.key);
    };
    useEffect(() => {
        const hashRouter: any = ref.current;
        const pathname = hashRouter?.history?.location?.pathname;
        let pageId;
        if (pathname === '/') {
            pageId = 'material';
        } else {
            pageId = pathname?.slice(1);
        }
        setCurrent(pageId);
    }, []);
    return (
        <div className="container">
            <Router ref={ref}>
                <Layout style={{ height: '100vh' }}>
                    <Sider
                    >
                        <Link onClick={handleLogoClick} to="/">
                            <div className="logo">
                                虚拟运营后台
                            </div>
                        </Link>
                        <Menu
                            theme="dark"
                            mode="inline"
                            onSelect={handleSelect}
                            selectedKeys={[current]}
                            defaultSelectedKeys={[current]}
                        >
                            <Menu.Item key="material">
                                <Link to="/">
                                    <span className="nav-text">原料配置</span>
                                </Link>
                            </Menu.Item>
                            <Menu.Item key="drink">
                                <Link to="/drink">
                                    <span className="nav-text">饮料配置</span>
                                </Link>
                            </Menu.Item>
                            <Menu.Item key="user">
                                <Link to="/user">
                                    <span className="nav-text">用户列表</span>
                                </Link>
                            </Menu.Item>
                            <Menu.Item key="deliver">
                                <Link to="/deliver">
                                    <span className="nav-text">配送员表</span>
                                </Link>
                            </Menu.Item>
                            <Menu.Item key="orders">
                                <Link to="/orders">
                                    <span className="nav-text">订单列表</span>
                                </Link>
                            </Menu.Item>
                        </Menu>
                    </Sider>
                    <Layout>
                        <Header className="header">
                            <UserOutlined style={{ marginRight: 5 }} />
                            root
                        </Header>
                        <Content style={{ margin: "24px 16px 0" }}>
                            <div
                                className="content"
                                style={{ padding: 24, minHeight: 360 }}
                            >
                                <Switch>
                                    <Route path="/" exact component={Material} />
                                    <Route path="/drink" exact component={Drink}/>
                                    <Route path="/user" component={User} />
                                    <Route path="/deliver" component={Deliver} />
                                    <Route path="/orders" component={Orders} />
                                </Switch>
                            </div>
                        </Content>
                        <Footer style={{ textAlign: "center" }}>
                            Virtual Manage Backend ©2020 Created by QinYongLi
                        </Footer>
                    </Layout>
                </Layout>
            </Router>
        </div>
    )
}

export default App
