import React from 'react';
import dayjs from 'dayjs';

export const getColumns = (action: Obj) => {
    const columns = [
        {
            title: "序号",
            dataIndex: "id",
            width: 50,
            render(value: string, record: any, index: number) {
                return index + 1;
            }
        },
        {
            title: "订单内容",
            dataIndex: "drink_form",
            render(drink_form: any) {
                return (
                    <>
                        { drink_form.map((item:any, index: number) => {
                            return (
                                <div key={index}>
                                    {
                                        item.class === 1 ? (
                                            <div>
                                                { item.form.name }
                                                <ul>
                                                    <li>
                                                        <span style={{marginRight: 5}}>数量:</span>
                                                        { item.form.value }
                                                    </li>
                                                    <li>
                                                        <span style={{marginRight: 5}}>价格:</span>
                                                        { item.price }
                                                    </li>
                                                </ul>
                                            </div>
                                        ) : (
                                            <div>
                                                自配饮料
                                                <ul>
                                                    <li>
                                                        <span style={{marginRight: 5}}>数量:</span>
                                                        { item.number }
                                                    </li>
                                                    <li>
                                                        <span style={{marginRight: 5}}>组成:</span>
                                                        { item.form.reduce((desc: '', fItem: any) => {
                                                            return `${desc}+${fItem.name}·${fItem.value}`
                                                        }, '').slice(1) }
                                                    </li>
                                                    <li>
                                                        <span style={{marginRight: 5}}>价格:</span>
                                                        { item.price }
                                                    </li>
                                                </ul>
                                            </div>
                                        )
                                    }
                                </div>
                            )
                        }) }
                    </>
                )
            }
        },
        {
            title: '金额',
            width: 50,
            dataIndex: "drink_form",
            render(drink_form: any) {
                let count = 0;
                drink_form.forEach((item: any) => {
                    if (item.class === 1) {
                        count += item.price * item.form.value;
                    } else {
                        count += item.price * item.number;
                    }
                })
                return (
                    <div>
                        { count }
                    </div>
                )
            }
        },
        {
            title: '订单时间',
            dataIndex: 'order_start_time',
            render(time: string) {
                return (
                    <div>{ dayjs(time).format('YYYY-MM-DD HH:mm:ss')}</div>
                )
            }
        },
        {
            title: "收货人",
            dataIndex: "user",
            width: 220,
            render(value: any, record: any) {
                return (
                    <>
                        <div>姓名: {record.user_name}</div>
                        <div>手机号码: {record.user_phone}</div>
                        <div>地址: {record.user_home + record.user_menpai}</div>
                    </>
                )
            }
        },
        {
            title: "配送人",
            dataIndex: "deliver",
            render(value: any, record: any) {
                return (
                    <>
                        <div>配送人id: {record.man_id}</div>
                        <div>手机号码: {record.man_phone}</div>
                    </>
                )
            }
        },
        {
            title: '订单状态',
            dataIndex: 'status',
            width: 100,
            render(value: number) {
                const map: {
                    [key: string]: any
                } = {
                    1: '制作中',
                    2: '配送中',
                    3: '订单完成'
                };
                return map[value];
            }
        }
    ];
    return columns;
}
